@import url(https://fonts.googleapis.com/css?family=Press+Start+2P&display=swap);
/* Coolors Exported Palette - coolors.co/cc444b-da5552-df7373-e39695-e4b1ab */

/* RGB */
:root {
    --shade1: rgba(204, 68, 75, 1);
    --shade2: rgba(218, 85, 82, 1);
    --shade3: rgba(223, 115, 115, 1);
    --shade4: rgba(227, 150, 149, 1);
    --shade5: rgba(228, 177, 171, 1);

    --error: #e76e55;
}
body {
  cursor: url('data:image/png;base64,'), auto;
}

.custom-pointer {
  cursor: url('data:image/png;base64,'), auto;
}
.main-content {
  margin-top: 5vh;
}

.main-nav {
  height: 5vh;
  z-index: 0;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  border-bottom: 2px solid darkslategrey; position: fixed;
}
.map.mode-create {
    cursor: crosshair;
}
.icon-style {
  margin: -50% 0 0 -50%;
  width: 20px;
  height: 40px;
}

.mario {
  width: inherit;
  height: inherit;
}
.list {
  margin: 0;
  padding: 0;
  width: 100%;
  list-style-type: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.list-item {
  margin: 0 1vw;
}

.logout-button {
  background-color: transparent;
  border: none;
  color: var(--error);
}
.modal {
  background-color: whitesmoke;
  font-size: x-small;
}

#location-modal-cancel {
  margin-left: 1vw;
}
.location-modal-spinner-parent {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.orientation-details {
  margin: 3vh 0;
  display: flex;
  flex-direction: row;
}

.radio-input {
  margin: 0.5rem;
}
